*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-bg-color: #f4f4f4;
  --primary-text-color: #111321;
  --color-btn-favorite: #111321;
  --white-color: #fff;
  --accent-color: #4440f6;
  --gray-color: #a8a8a8;
  --footer-color: #7a7a7a;
  --switcher-off: #5f6775;
  --switcher-on: #4b48db;
}

:root body.darkMode {
  --bg-color: #2e2e2e;
  --text-color: #f4f4f4;
  --switcher-dark-off: #f4f4f4;
  --switcher-dark-on: #4440f7;
}

body {
  min-height: 100vh;
  z-index: -999;
  background-color: var(--bg-color);
  color: var(--text-color);
  flex-direction: column;
  justify-content: space-between;
  font-family: Manrope, sans-serif;
  display: flex;
  position: relative;
}

main {
  z-index: -999;
  flex-grow: 1;
  padding-bottom: 65px;
  position: relative;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  main {
    padding-bottom: 100px;
  }
}

@media screen and (min-width: 1280px) {
  main {
    padding-bottom: 175px;
  }
}

.container {
  max-width: 480px;
  min-width: 320px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
  display: block;
}

address {
  font-style: inherit;
}

button {
  background-color: inherit;
  box-shadow: none;
  cursor: pointer;
  border: none;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

svg {
  fill: currentColor;
}

.menu-container {
  width: 100%;
  height: 100%;
  background-color: var(--white-color);
  z-index: 999;
  padding: 24px 16px 18px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

@media screen and (min-width: 768px) {
  .menu-container {
    display: none;
  }
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  color: var(--primary-text-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 28px;
  right: 16px;
}

.mobile-menu {
  letter-spacing: .02em;
  margin-top: 0;
  margin-bottom: 302px;
  line-height: 1.175;
}

.mobile-menu__item {
  height: 44px;
  color: var(--primary-text-color);
  justify-content: space-between;
  align-items: center;
  margin-bottom: 18px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.375;
  display: flex;
}

.mobile-menu__current {
  background-color: var(--accent-color);
  color: var(--white-color);
  background-size: cover;
  margin: 0 -25px 18px -16px;
  padding-left: 16px;
  padding-right: 16px;
}

.menu-both {
  align-items: center;
  gap: 14px;
  display: flex;
}

.mobile-menu .link {
  text-decoration: none;
}

.menu-icon {
  border: 1px solid var(--white-color);
  border-radius: 50%;
}

.nav-close {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 68px;
  display: flex;
}

.menu-icon__arrow {
  stroke: var(--white-color);
  fill: rgba(0, 0, 0, 0);
  margin-right: 25px;
}

.theme {
  align-items: center;
  gap: 8px;
  display: flex;
  position: absolute;
  bottom: 18px;
  left: 16px;
}

.switch {
  width: 40px;
  height: 20px;
  margin: 0;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  border: .604701px solid var(--switcher-on);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: var(--switcher-on);
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 2px;
}

input:checked + .slider {
  background-color: var(--switcher-on);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--accent-color);
}

input:checked + .slider:before {
  background-color: var(--white-color);
  transform: translateX(19px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

input:checked + .slider.round {
  background-color: var(--switcher-on);
  border: .604701px solid var(--white-color);
}

.theme-icon {
  margin: 0;
}

.theme-icon__light {
  fill: var(--switcher-on);
}

.theme-icon__dark, input:checked + .theme.theme-icon__light {
  fill: var(--switcher-off);
}

input:checked + .theme.theme-icon__dark {
  fill: var(--switcher-on);
}

.header {
  min-height: 81px;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  padding: 24px 0;
}

.header__container {
  justify-content: space-between;
