  }
}

.logo-mobile {
  visibility: visible;
}

@media screen and (min-width: 767.98px) {
  .logo-mobile {
    visibility: hidden;
  }
}

@media screen and (min-width: 1279.98px) {
  .logo-mobile {
    visibility: hidden;
  }
}

.nav {
  align-items: baseline;
  display: flex;
}

@media screen and (min-width: 767.98px) {
