  flex: none;
  order: 0;
  font-family: Poppins;
  font-size: 18px;
  line-height: 24px;
}

.mobile-btn-open .icon {
  fill: var(--primary-text-color);
}

@media screen and (min-width: 767.98px) {
  .mobile-btn-open {
    display: none;
  }
}

.switch {
  width: 40px;
  height: 20px;
  align-items: center;
  display: flex;
}

.switch__checkbox {
  display: none;
}

.wrapper-toggle {
  width: 100%;
  height: 100%;
  border: 1px solid var(--switcher-moon-light);
  border-radius: 20px;
  align-items: center;
  margin-left: 0;
  padding: 1px;
  transition: margin-left 1s ease-in;
  display: flex;
}

.toggle {
  width: 16px;
  height: 16px;
  background-color: var(--switcher-moon-light);
  border-radius: 50%;
}

.switch__checkbox:checked + .wrapper-toggle .toggle {
  background-color: var(--white-color);
  margin-left: auto;
}

.switch__checkbox:checked + .wrapper-toggle {
  border-color: var(--white-color);
  background-color: var(--switcher-moon-light);
}

.filter {
  padding-top: 28px;
}

.filter__container {
  opacity: 0;
  justify-content: space-between;
  align-items: center;
  transition: opacity .25s ease-in-out, transform .25s ease-in-out;
  display: flex;
  transform: translateY(-100%);
}

.filter__container.isFilled {
  opacity: 1;
  transform: translateY(0%);
}

.filter__wrap {
  align-items: center;
  gap: 16px;
  display: flex;
}

.main-categories {
  display: none;
}

@media screen and (min-width: 768px) {
  .main-categories {
    gap: 16px;
    display: flex;
  }
}

.main-categories__btn, .dropdown-btn {
  text-transform: capitalize;
  color: var(--accent-color);
  background-color: var(--white-color);
  border: 1px solid;
  border-radius: 20px;
  outline: none;
  padding: 8px 22px;
  font-size: 12px;
  line-height: 1.33333;
  transition: color .25s ease-in-out, background-color .25s ease-in-out;
}

.main-categories__btn.isSelected, .isSelected.dropdown-btn, .main-categories__btn:hover, .dropdown-btn:hover, .main-categories__btn:focus, .dropdown-btn:focus {
  color: var(--white-color);
  background-color: var(--accent-color);
}

.filter__others {
  position: relative;
}

.dropdown-btn {
  align-items: center;
  transition: color .25s ease-in-out, background-color .25s ease-in-out;
  display: flex;
}

.dropdown-btn.isActive {
  color: var(--white-color);
  background-color: var(--accent-color);
}

.dropdown-btn__text {
  pointer-events: none;
  margin-right: 10px;
}

.dropdown-btn__icon {
  pointer-events: none;
  transition: transform .25s ease-in-out;
}

.dropdown-list {
  display: none;
}

.dropdown-list[data-simplebar] {
  z-index: 999;
  max-height: 380px;
  width: max-content;
  min-width: 100%;
  background-color: var(--white-color);
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform-origin: top;
  border-radius: 20px;
  flex-direction: column;
  padding: 8px 0;
  transition: transform .25s ease-in-out, opacity .25s ease-in-out, visibility .25s ease-in-out;
  display: flex;
  position: absolute;
  top: 100%;
  left: 0;
  transform: translateY(-10%)scaleY(0);
}

.dropdown-list[data-simplebar] .simplebar-scrollbar:before {
  width: 5px;
  background-color: var(--accent-color);
}

.dropdown-list[data-simplebar] .simplebar-scrollbar.simplebar-visible:before {
  opacity: .7;
}

.dropdown-list.isOpen {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
  transform: translateY(.01%)scaleY(1);
}

.dropdown-list.isOpen + .dropdown-btn .dropdown-btn__icon {
  transform: rotate(180deg);
}

.dropdown-list__btn {
  color: var(--accent-color);
  text-transform: capitalize;
  padding: 6px 14px;
  font-size: 12px;
  line-height: 1.33333;
}

.dropdown-list__btn.isSelected, .dropdown-list__btn:hover, .dropdown-list__btn:focus {
  outline: none;
  text-decoration: underline;
}

@media screen and (min-width: 768px) {
  .dropdown-list__btn {
    padding: 6px 24px;
  }
}

.simplebar-content {
  width: max-content;
  min-width: 100%;
}

:root {
  --blue: #4440f6;
  --main-white: #fff;
  --main-background: #f8f8f8;
  --incative-text: rgba(60, 60, 67, .3);
  --border-inactive: #a2a2a2;
}

.calendar-form {
  align-items: center;
  display: flex;
}

.calendar-form__text {
  margin-right: 40px;
  font-weight: 700;
  display: none;
}

@media screen and (min-width: 1280px) {
  .calendar-form__text {
    display: block;
  }
}

.choseDataButton {
  width: 151px;
  height: 33px;
  background-color: var(--main-white);
  color: var(--incative-text);
  border: 1px solid #111321;
  border-radius: 20px;
  margin-bottom: 2px;
  font-family: Manrope;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  transition: background-color .25s ease-in-out;
}

@media screen and (min-width: 768px) {
  .choseDataButton {
    width: 168px;
  }
}

.choseDataButton:hover, .choseDataButton-active {
  background-color: var(--blue);
  color: var(--main-white);
  border: 0;
}

.choseDataButton:hover ~ .calendar-icon, .choseDataButton:hover ~ .dropdown-icon {
  color: var(--main-white);
  transition: transform .25s ease-in-out;
}

.container-calendar {
  width: max-content;
  height: max-content;
  height: 100%;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  justify-content: center;
  transition: opacity .25s ease-in-out, visibility .3s ease-in-out;
  position: absolute;
  top: 35px;
  left: -80px;
}

.container-calendar--active {
  opacity: 1;
  visibility: visible;
  transition: transform .25s ease-in-out;
}

.calendar {
  height: 250px;
  width: 250px;
  background-color: var(--main-background);
  letter-spacing: -.408px;
  font-feature-settings: "case" on;
  border-radius: 20px;
  padding: 27px 20px 16px 16px;
  font-family: Manrope;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 22px;
}

.calendar-header {
  height: 26px;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 13px;
  font-size: 16px;
  display: flex;
}

.mounth-year-display {
  padding-right: 8px;
}

.header-left-block {
  justify-content: space-between;
  display: flex;
}

.mounth-picker {
  width: 60px;
  justify-content: space-between;
  display: flex;
}

.calendarPicker {
  position: relative;
}

.calendar-icon {
  pointer-events: none;
  color: var(--blue);
  transition: transform .25s ease-in-out;
  position: absolute;
  top: 10px;
  left: 10px;
}

.calendar-icon--active {
  color: var(--main-white);
}

.dropdown-icon {
  pointer-events: none;
  color: var(--incative-text);
  rotate: 0;
  transition: all .25s ease-in-out;
  position: absolute;
  top: 10px;
  right: 10px;
}

.dropdown-icon--active {
  color: var(--main-white);
  transform: rotate(180deg);
}

.year-change {
  width: 20px;
  height: 20px;
  color: var(--blue);
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  display: flex;
  rotate: -90deg;
}

.year-change:hover {
  background-color: var(--blue);
  color: var(--main-white);
  cursor: pointer;
  border-radius: 20px;
}

.calendar-week-days {
  color: var(--incative-text);
  cursor: pointer;
  justify-content: space-between;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.calendar-week-days div {
  align-items: center;
  justify-items: center;
  display: grid;
}

.calendar-days {
  height: 150px;
