  margin: 0;
  padding: 0;
  position: relative;
  overflow: hidden;
  box-sizing: inherit !important;
}

.simplebar-height-auto-observer {
  box-sizing: inherit;
  opacity: 0;
  height: 1000%;
  width: 1000%;
  min-height: 1px;
  min-width: 1px;
  pointer-events: none;
  z-index: -1;
  display: block;
  position: absolute;
  top: 0;
